// shengqu
// import ReviewBar from '@/components/review-bar.vue'; //评论条
// 缺user页面,默认直接跳user
<template>
<!--评论列表 start-->
	   <div class="discussBar">
	   	   <h3 class="discussBar_title" v-if="vTitle!=''">{{vTitle}}</h3>
	   	   <div class="discussContent clearfix" v-for="(item,index) in vData" :key="item.id">
	   	   	   <div class="discuss_portrait">
	   	   	   	  <img :src="item.user_profile.profile.avatar_path"  alt="" class="discuss_portrait_img" @click="go(item.user_profile.id)"/>
	   	   	   </div>
	   	   	   <div class="discuss_content_right">
	   	   	   	  <div style="overflow: hidden;">
	   	   	   	  	  <span class="discussBar_name" @click="go(item.user_profile.id)">{{item.user_profile.username}}</span>
	   	   	   	  	  <span class="discussBar_time">{{item.created_at}}</span>
	   	   	   	  	  <div class="discussBar_love_box">
											<img src="@/assets/comment-bar/in_like_border_icon@2x.png" alt="" v-show="!item.like_state" @click="commentLove(item,false)" />
											<img src="@/assets/comment-bar/in_like_icon@2x.png" alt="" v-show="item.like_state" @click="commentLove(item,true)"/>
											<span class="discussBar_love_count">{{item.likes}}</span>
	   	   	   	  	  </div>
	   	   	   	  </div>
	   	   	   	  <div class="discussBar_name_one">
									{{item.content}}
	   	   	   	  </div>
	   	   	   	  <div class="discussBar_name_another" v-if="item.kids" @click="goReviewDetails(item.id)">
	   	   	   	  	    <p class="discussBar_commentator_show" v-for="kid in item.kids" v-if="item.kids.length>0" :key="kid.id">
	   	   	   	  	    	  <a href="javascript:;" class="discussBar_commentator">{{kid.user_profile.username}}</a>
	   	   	   	  	    	  <span class="discussBar_commentator_word">{{kid.content}}</span>
	   	   	   	  	    </p>
	   	   	   	  	   <a class="discussBar_checkMore" v-if="item.kids.length>0">共 {{item.kids.length}} 条回复&nbsp;></a>
	   	   	   	  </div>
								<!-- 一条线 -->
								<div :class="!index==vData.length-1? 'discussBar_bottomLine':'btn-secondary'"></div>
	   	   	   </div>
	   	        
	   	   </div>
	   	
	   </div>
	  <!--评论列表 end-->
</template>
<script>
import { nativeGo,} from '@/assets/js/bridge.js'; // 引入桥
import { isApp } from '@/assets/js/env'
export default {
  name: 'discussBar',
  props: ['vTitle','vLimit','vData'],
  data () {
    return {
			discussLists:'',
    }
  },
  methods:{
  	  commentLove(item,_isCancel){
				if(!_isCancel){
					item.likes++
				}else{
					item.likes--
				}
				this.$emit('getLikeIndex',{id:item.id,isCancel:_isCancel})
				item.like_state = !item.like_state
			},
			go(_id){
				nativeGo('user',{id:_id})
				if(!isApp){}
			},
			goReviewDetails(_id){
				nativeGo('review-details',{id:_id})
			}
  },
  mounted(){
	  	
  }
  

}
</script>
<style scoped lang="less">
  @m:750/375;
 /* 评论列表 start*/
  .discussBar{
  	   width: 100%;overflow:hidden;
  }
  .discussBar_title{
  	    font-family: PingFangSC-Medium;
			font-size: 14px*@m;
			color: #0F2540;
			letter-spacing: 0;
			line-height: 14px*@m;
			margin-top: 30px*@m;
  }
  .discussContent{
  	  width: 100%;
  	  margin-top:20px*@m;
  	  /*overflow: hidden;*/
  }
  .discuss_portrait{
  	   width: 40px*@m;
  	   height: 40px*@m;
  	   float: left;
  	   border-radius: 50%;
  	   background: darkgrey;
  	   overflow: hidden;
  }
  .discuss_portrait_img{
  	   width: 100%;
  	   height: 100%;
  	   object-fit:cover;
  }
  .discuss_content_right{
  	   width:calc(100% - 74px*@m);
  	   float: right;
  	   padding-bottom: 20px*@m;
  	   padding-top: 2px*@m;
  	   position: relative;
  }
  .discussBar_name{
  	    font-family: PingFangSC-Medium;
			font-size: 14px*@m;
			color: #435771;
			letter-spacing: 0;
			line-height: 14px*@m;
			float: left;
  }
  .discussBar_time{
  	    font-family: PingFangHK-Regular;
			font-size: 12px*@m;
			color: #C6C6C6;
			letter-spacing: 0;
			line-height: 12px*@m;
			margin-left:6px*@m;
			float: left;
  }
  .discussBar_love_box{
  	   float:right;
  }
  .discussBar_love_box img{
  	   float: left;
  	   width: 18px*@m;
  	   height: 16px*@m;
  }
  .discussBar_love_count{
  	  font-family: PingFangSC-Medium;
		font-size: 12px*@m;
		color: #C9D6DE;
		letter-spacing: 0;
		line-height: 12px*@m;
		float: left;
		margin-top: 3px*@m;
		margin-left: 8px*@m;
  }
  .discussBar_name_one{
  	  width:100%;
  	  margin-top: 10px*@m;
  	  font-family: PingFangSC-Regular;
		font-size: 16px*@m;
		color: #0F2540;
		letter-spacing: 0;
		text-align: justify;
		line-height: 22px*@m;
  }
  .discussBar_name_another{
  	   width:100%;
  	   margin-top:14px*@m;
  	   background: #F3F3F3;
     border-radius: 6px*@m;
  }
  .discussBar_commentator_show{
		font-family: PingFangSC-Regular;
		font-size: 14px*@m;
		color: #061300;
		letter-spacing: 0;
		line-height: 24px*@m;
		padding-left: 10px*@m;
		padding-right: 13px*@m;
		margin-top: 5px;
  }
  .discussBar_commentator_show:first-child{
  	  padding-top: 5px*@m;margin-top: 0;
  }
  .discussBar_commentator_show:last-of-type{
  	  padding-bottom: 5px*@m;
  }
  .discussBar_commentator{
			color: #649BE1;
			text-decoration: none;
  }
  .discussBar_commentator_word{
  	   
  }
  .discussBar_checkMore{
  	  font-family: PingFangSC-Medium;
		font-size: 14px*@m;
		color: #649BE1;
		letter-spacing: 0;
		line-height: 14px*@m;
		display: inline-block;
		margin-left: 10px*@m;
		padding-bottom: 10px*@m;
  }
  .btn-secondary{
  	  border-bottom: 0;
  }
  .discussBar_bottomLine{
		position: absolute;
		width: calc(100% + 20px*@m);
		height:2px*@m;
		background: #EFEFEF;
		right: -20px*@m;
		bottom: 0;
  }
 /* 评论列表 end*/
	
</style>


